header, .mainDiv {
  color: #545454;
  max-width: 90%;
  justify-content: center;
  margin-left: 5%;
}

#title {
  justify-content: center;
  text-align: center;
  padding-top: 10%;
}

.mainDiv {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

input[type="text"] {
  display: block;
  margin: auto;
  text-align: center;
  width: 80%;
  min-height: 20px;
  border-radius: 5px;
  font-size: 12px;
  margin-bottom: 10%;
  margin-top: 10%;
}

.buttonContainer {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: flex-start;
  flex-wrap: wrap;
  height: 100px;
}

input[type="button"] {
  background-color: transparent;
  color: #008CBA;
  -webkit-transition-duration: 0.4s; /* Safari */
  transition-duration: 0.4s;
}

input[type="button"]:hover {
  background-color: #008CBA;
  color: white;
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}
